.svg-symbols {
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

[class^='icon-svg-'], [class*=' icon-svg-'] {
  fill: currentColor;
  stroke: inherit;
}

[class^='icon-svg-'] use, [class*=' icon-svg-'] use {
  pointer-events: none;
}

.icon-svg-angle-1-down {
  width: 1.66667em;
  height: 1em;
}

.icon-svg-angle-left {
  width: 0.66631em;
  height: 1em;
}

.icon-svg-buy-mhc {
  width: 1em;
  height: 1em;
}

.icon-svg-buy {
  width: 1em;
  height: 1em;
}

.icon-svg-envelope {
  width: 1.63517em;
  height: 1em;
}

.icon-svg-key {
  width: 0.938em;
  height: 1em;
}

.icon-svg-logo_mail {
  width: 4.80769em;
  height: 1em;
}

.icon-svg-metaapps {
  width: 1.35771em;
  height: 1em;
}

.icon-svg-metawallet {
  width: 1.17592em;
  height: 1em;
}

.icon-svg-refresh {
  width: 1.29351em;
  height: 1em;
}

.icon-svg-safe {
  width: 0.83378em;
  height: 1em;
}

.icon-svg-settings {
  width: 1em;
  height: 1em;
}

.icon-svg-user {
  width: 1.0661em;
  height: 1em;
}

.btn {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
  cursor: pointer;
}

.btn, .btn:hover, .btn:focus {
  text-decoration: none;
}

.btn:disabled, .btn.disabled,
fieldset[disabled] .btn {
  cursor: default;
  opacity: .65;
}

.btn--primary {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;
  color: white;
  text-transform: uppercase;
  padding: 17.5px 20px;
  border: 0;
  border-radius: 4px;
  background: #2F7BC2;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn--primary:hover:not(:disabled) {
  background: #2a6ead;
}

.container-fluid {
  max-width: 1280px;
}

.content h1, .content h2, .content h3, .content h4, .content h5, .content h6,
.content .h1, .content .h2, .content .h3, .content .h4, .content .h5, .content .h6 {
  font-weight: 300;
  line-height: 1.16667;
  margin-bottom: 2.5px;
}

.content h1, .content .h1 {
  font-weight: 100;
  font-size: 60px;
}

.content h2, .content .h2 {
  font-size: 50px;
}

.content h3, .content .h3 {
  font-size: 40px;
}

.content h4, .content .h4 {
  font-size: 30px;
}

.content h5, .content .h5 {
  font-size: 25px;
}

.content h6, .content .h6 {
  font-size: 20px;
}

.footer__inner {
  display: flex;
  align-items: flex-start;
}

.footer__logo {
  font-size: 52px;
  position: fixed;
  right: 70px;
  bottom: 70px;
}

.footer__logo, .footer__logo:hover, .footer__logo:focus {
  color: white;
  text-decoration: none;
}

.footer__logo-img {
  display: block;
}

.form-checkbox {
  font-size: 14px;
  line-height: 1.14286;
  display: inline-flex;
  vertical-align: top;
  align-items: flex-start;
  margin: 0;
}

.form-checkbox__input {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.form-checkbox__fake {
  flex: none;
  width: 18px;
  height: 18px;
  padding: 4px;
  border: 1px solid #99CEFF;
  cursor: pointer;
}

.form-checkbox__fake::before {
  content: '';
  display: block;
  height: 100%;
  background: #99CEFF;
  transition: 0.15s ease-in-out;
  transform: scale(0);
}

:focus ~ .form-checkbox__fake {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

:checked ~ .form-checkbox__fake::before {
  transform: scale(1);
}

.form-checkbox__lbl {
  margin-top: 1px;
  margin-left: 14px;
}

.form-field-lbl {
  display: block;
  margin: 0;
  position: relative;
}

.form-field-lbl__input {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.25;
  color: #99CEFF;
  text-transform: uppercase;
  display: block;
  width: 100%;
  padding: 33.5px 20px 7.5px;
  border: 1px solid transparent;
  background: rgba(5, 11, 31, 0.2);
  box-shadow: 0 0 10px rgba(153, 206, 255, 0.2);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-field-lbl__placeholder {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.25;
  color: #99CEFF;
  text-transform: uppercase;
  padding: 20.5px 20px;
  border: 1px solid transparent;
  cursor: text;
  pointer-events: none;
  transition: 0.15s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.form-field-lbl__icon {
  color: #99CEFF;
  width: 20px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  cursor: text;
  pointer-events: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-field-lbl__icon.icon-svg-envelope {
  font-size: 11px;
}

.form-field-lbl__icon.icon-svg-safe {
  font-size: 18px;
}

.form-field-lbl--focus .form-field-lbl__input, .form-field-lbl--has-value .form-field-lbl__input {
  color: white;
  border-color: rgba(153, 206, 255, 0.3);
  background: rgba(5, 11, 31, 0.6);
  box-shadow: none;
  outline: 0;
}

.form-field-lbl--focus .form-field-lbl__placeholder, .form-field-lbl--has-value .form-field-lbl__placeholder {
  font-size: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.form-field-lbl--focus .form-field-lbl__icon, .form-field-lbl--has-value .form-field-lbl__icon {
  color: white;
}

.form-start {
  font-size: 14px;
  line-height: 1.14286;
}

.form-start__header {
  margin-bottom: 20px;
}

.form-start__title {
  font-size: 24px;
  line-height: 1.16667;
}

.form-start__link {
  color: #99CEFF;
  text-align: left;
  display: inline-flex;
  vertical-align: top;
  align-items: flex-start;
  transition: color 0.15s ease-in-out;
  margin-top: 10px;
}

.form-start__link, .form-start__link:hover, .form-start__link:focus {
  text-decoration: none;
}

.form-start__link:hover {
  color: white;
}

.form-start__link-icon {
  flex: none;
  font-size: 16px;
  margin-right: 8px;
}

.form-start__link-icon.icon-svg-key {
  transform: translateY(-1px);
}

.form-start__link-icon.icon-svg-user {
  font-size: 15px;
}

.form-start__row {
  margin-bottom: 20px;
}

.form-start__link-continue {
  color: #99CEFF;
  font-size: 24px;
  line-height: 1.16667;
  transition: color 0.15s ease-in-out;
}

.form-start__link-continue, .form-start__link-continue:hover, .form-start__link-continue:focus {
  text-decoration: none;
}

.form-start__link-continue:hover {
  color: white;
}

.header {
  padding: 19px 10px;
  background: linear-gradient(180deg, #F2F2F2 57.46%, #EBEBEB 100%);
}

.header__inner {
  display: flex;
}

.header__btn {
  color: #636363;
  font-size: 15px;
  line-height: 1;
  border: 0;
  padding: 0;
  background: none;
  width: 32px;
  height: 32px;
  display: flex;
  flex: none;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.header__btn:hover:not(:disabled) {
  background: #e6e6e6;
}

.header__btn:disabled {
  color: #D9D9D9;
  cursor: default;
}

.header__btn--forward {
  transform-origin: 50% 50% 0;
  transform: rotateY(180deg);
}

.header__btn--reload {
  font-size: 17px;
}

.header__loc {
  flex: 1 1 auto;
  margin-left: 8px;
  margin-right: 50px;
}

.header__loc-field {
  color: #636363;
  font-size: 16px;
  border-radius: 4px;
  border: 0;
  padding: 0 40px 0 15px;
  width: 100%;
  height: 32px;
  background: url('data:image/svg+xml;charset=utf-8,%3Csvg%20width%3D%2210%22%20height%3D%226%22%20viewBox%3D%220%200%2010%206%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20xmlns%3Axlink%3D%22http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink%22%3E%3Cuse%20xlink%3Ahref%3D%22%23path0_stroke%22%20transform%3D%22translate(1.04%201.199)%22%20fill%3D%22%238A8A8A%22%2F%3E%3Cdefs%3E%3Cpath%20id%3D%22path0_stroke%22%20d%3D%22M8.31.707A1%201%200%201%200%206.894-.707L8.309.707zM3.8%203.801l-.707.707a1%201%200%200%200%201.414%200L3.8%203.801zM.707-.707A1%201%200%200%200-.707.707L.707-.707zm6.188%200l-3.801%203.8%201.414%201.415L8.31.708%206.895-.708zm-2.387%203.8l-3.8-3.8L-.708.707l3.8%203.801%201.415-1.414z%22%2F%3E%3C%2Fdefs%3E%3C%2Fsvg%3E') no-repeat right 15px center #FFFFFF;
}

.header__loc-field:focus {
  outline: 0;
}

.header__menu {
  margin-left: auto;
  margin-right: 22px;
}

.menu {
  display: flex;
  overflow: hidden;
}

.menu__item {
  color: #636363;
  text-decoration: none;
  font-size: 16px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  overflow: hidden;
  transition: color 0.15s ease-in-out;
}

.menu__item:hover, .menu__item:focus {
  color: #141414;
  text-decoration: none;
}

.menu__item + .menu__item {
  margin-left: 32px;
}

.menu__item-icon {
  flex: none;
  margin-right: 5px;
}

.menu__item-icon.icon-svg-buy {
  font-size: 17px;
}

.menu__item-icon.icon-svg-metaapps {
  font-size: 14px;
}

.menu__item-icon.icon-svg-metawallet {
  font-size: 17px;
}

.menu__item-icon.settings {
  font-size: 17px;
}

.menu__item-lbl {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 1px;
}

.page {
  height: 100%;
}

.page ::selection {
  color: white;
  background-color: #99CEFF;
}

.page__body {
  height: 100%;
}

.page__body-inner {
  display: flex;
  flex-direction: column;
  min-width: 1280px;
  min-height: 100%;
  background: url("../img/promo-spiral.png") no-repeat 0 0 fixed, url("../img/team-cloud.png") no-repeat 100% 100% fixed #03081C;
  padding: 70px 0;
}

.page__header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.page__main {
  flex: 1 0 auto;
  padding: 30px 0;
}

.page__footer {
  flex: none;
}

.title-sect {
  font-weight: 100;
  font-size: 60px;
  line-height: 1.16667;
  color: white;
  margin: 0 0 30px;
}

.title-sect strong {
  font-weight: inherit;
  color: #99CEFF;
}
